import auth from '../../common/auth.js';
export default {
	data() {
		return {
			textNasd: null,
			userName: ''
		};
	},
	onShow(){
		this.userName = uni.getStorageSync('userName')
		auth.getToken()
		uni.getNetworkType({
			success: function(res) {
				console.log(res.networkType);
				if (res.networkType == 'none') {
					uni.reLaunch({
						url: '/pages/notNetwork/notNetwork'
					});
				}
			}
		});
		// console.log(this.userName);
		if(!this.userName){
			uni.redirectTo({
				url: '/pages/login/login'
			});
		}
	},
	onLoad() {
		// this.userName = uni.getStorageSync('userName')
	},
	onReachBottom() {},
	onPullDownRefresh() {},
	methods: {
		select() {
			let t = this;
			uni.scanCode({
				onlyFromCamera: true,
				success: function(res) {
					console.log(res);
					uni.navigateTo({
						url: '/pages/veriy/index?id=' + res.result
					});
				},
				fail: function(err) {
					console.log(err);
				}
			});
		},
		navigato(){
			uni.clearStorageSync();
			uni.redirectTo({
				url: '/pages/login/login'
			});
		},
		setupPwd(){
			uni.navigateTo({
				url: '/pages/setPwd/setPwd'
			});
		}
	}
};
